.NavMobile {
  display: none;
  justify-content: space-between;
  margin-top: 15px; }

.MobileMenu {
  top: 60px;
  position: fixed;
  left: 0;
  width: 100%;
  height: 100%;
  background: #383838;
  z-index: 120;
  transform-origin: 95% -5%;
  transform: scale(0); }
  .MobileMenu::before {
    content: "";
    position: absolute;
    top: -12px;
    right: 30px;
    width: 25px;
    height: 12px;
    background: #383838;
    clip-path: polygon(50% 0, 0% 100%, 100% 100%); }

.MobileMenuList {
  display: flex;
  flex-wrap: wrap;
  height: 50%;
  width: 100%;
  max-width: 600px;
  margin: 2px auto 0 auto; }
  .MobileMenuList li {
    width: 50%;
    padding: 2px; }
    .MobileMenuList li a {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      width: 100%;
      height: 100%;
      text-align: center;
      border: 2px solid #d8b093;
      transition: background 0.5s, color 0.5s; }
      .MobileMenuList li a:hover, .MobileMenuList li a:focus {
        background: #383838;
        color: white; }
      .MobileMenuList li a span {
        font-size: 80px;
        display: block;
        width: 100%; }

.MobileMenuUserPanel {
  display: flex;
  justify-content: space-around;
  margin: 50px 0; }
  .MobileMenuUserPanel button {
    font-size: 14px;
    min-width: 150px;
    height: 60px;
    border: 1px solid transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    background: black;
    padding: 6px 12px;
    color: white;
    margin: 0 5px; }
    .MobileMenuUserPanel button:focus {
      outline-color: #d8b093; }
    .MobileMenuUserPanel button:hover {
      cursor: pointer; }
  .MobileMenuUserPanel .SignUp {
    background: #d8b093; }

.MobileMenuUserPanel .btn-singin, .mobile-menu-user-panel .btn-singup {
  min-width: 150px;
  margin: 0 10px; }

@media screen and (max-width: 920px) {
  .NavMobile {
    display: flex; } }

.Show {
  transition: all 1s;
  opacity: 1;
  transform: scale(1); }

.Hide {
  transition: all 1s;
  opacity: 0;
  transform: scale(0); }

@media screen and (max-width: 576px) {
  .mobile-menu-list {
    margin: 2px 0 0 0; } }



