@charset "UTF-8";
.Navigation {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .Navigation .NavigationMenu {
    display: flex; }
    .Navigation .NavigationMenu li {
      position: relative; }
      .Navigation .NavigationMenu li::before {
        content: "◆";
        position: absolute;
        left: 0px;
        top: 50%;
        transform: translateY(-50%);
        color: #d8b093;
        opacity: 0;
        transition: opacity 0.4s; }
      .Navigation .NavigationMenu li:hover::before {
        opacity: 1; }
      .Navigation .NavigationMenu li a {
        position: relative;
        display: block;
        color: #383838;
        font-size: 20px;
        padding: 10px 16px;
        font-family: "sofiapro-light"; }
        .Navigation .NavigationMenu li a:hover, .Navigation .NavigationMenu li a:focus {
          font-weight: bold; }

@media screen and (max-width: 920px) {
  .Navigation {
    display: none; } }



