.NavigationBtn {
  width: 25px;
  height: 25px;
  padding: 0;
  border: 1px solid transparent;
  position: relative;
  display: none;
  background: transparent;
  font-size: 14px;
  cursor: pointer; }
  .NavigationBtn:focus {
    outline-color: #d8b093; }
  .NavigationBtn span {
    width: 22px;
    height: 2px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #383838;
    transition: background 0.4s; }
    .NavigationBtn span::before, .NavigationBtn span::after {
      content: "";
      position: absolute;
      width: 22px;
      height: 2px;
      background: #383838;
      left: 50%;
      transform: translateX(-50%);
      transition: top 0.5s, left 0.5s, transform 0.5s; }
    .NavigationBtn span::before {
      top: 8px; }
    .NavigationBtn span::after {
      top: -8px; }

.Active span {
  background: transparent; }
  .Active span::before {
    top: 0;
    left: 0;
    transform: rotate(45deg); }
  .Active span::after {
    top: 0;
    left: 0;
    transform: rotate(135deg); }

@media screen and (max-width: 920px) {
  .NavigationBtn {
    display: block; } }



