@charset "UTF-8";
.SliderItem {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  padding: 0 50px;
  transition: opacity 1s;
  opacity: 1;
  position: absolute;
  left: 0;
  top: 0; }
  .SliderItem .SliderContent {
    width: 50%;
    position: relative;
    z-index: 1;
    margin: 0 0 0 50px; }
    .SliderItem .SliderContent .SliderContentTitle {
      text-transform: uppercase;
      font-weight: bold;
      color: #383838;
      margin: 0 0 20px 0;
      font-size: 26px; }
    .SliderItem .SliderContent .SliderContentDescription {
      position: relative;
      padding: 0 0 10px 0; }
      .SliderItem .SliderContent .SliderContentDescription::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        height: 1px;
        width: 140%;
        background: #383838; }
      .SliderItem .SliderContent .SliderContentDescription::after {
        content: "●";
        position: absolute;
        left: 139%;
        bottom: -6px; }
    .SliderItem .SliderContent .SliderContentPayment {
      display: flex;
      align-items: center;
      margin: 50px 0 0 0; }
      .SliderItem .SliderContent .SliderContentPayment .SliderContentPrice {
        color: #d8b093;
        font-size: 40px;
        font-weight: bold;
        margin: 0 0 7px 25px; }
  .SliderItem .SliderImg {
    width: 50%;
    margin: 0 100px 0 0;
    position: relative; }
    .SliderItem .SliderImg img {
      width: 100%;
      height: auto; }
    .SliderItem .SliderImg img:last-of-type {
      position: absolute;
      left: 0;
      bottom: -40%;
      transform: scaleY(-1);
      opacity: 0.14; }

.BtnBuy {
  background: transparent;
  border: 1px solid #383838;
  padding: 0;
  font-size: 14px;
  padding: 10px 40px;
  transition: background 0.4s, color 0.4s; }
  .BtnBuy:focus {
    outline-color: #d8b093; }
  .BtnBuy:hover {
    cursor: pointer; }

@media screen and (max-width: 576px) {
  .BtnBuy {
    padding: 10px 20px; } }

@media screen and (max-width: 1024px) {
  .slider-item {
    padding: 0; } }

@media screen and (max-width: 920px) {
  .SliderItem {
    flex-wrap: wrap;
    align-items: baseline;
    justify-content: center; }
    .SliderItem .SliderContent {
      margin: 0;
      width: 100%;
      order: 1;
      margin: 25px 0;
      text-align: center; }
      .SliderItem .SliderContent .SliderContentDescription::before, .SliderItem .SliderContent .SliderContentDescription::after {
        content: none; }
      .SliderItem .SliderContent .SliderContentPayment {
        justify-content: center;
        margin: 25px 0 0 0; }
  .SliderImg {
    margin: 60px 0 0 0; } }

@media screen and (max-width: 768px) {
  .SliderItem {
    align-items: flex-start; }
    .SliderItem .SliderContent {
      margin: 0; }
      .SliderItem .SliderContent .SliderContentDescription {
        padding: 0 10px; }
  .SliderImg {
    width: 90%; } }



