.ContactData {
  background-color: #2C2C2C;
  height: 100vh;
  width: 100vw;
  text-align: center;
  box-shadow: 0 2px 3px #ccc;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  position: relative; }
  .ContactData h4 {
    width: 100%;
    color: rgba(248, 243, 234, 0.7);
    font-weight: 900;
    font-size: 60px; }
  .ContactData form {
    max-width: 920px;
    display: flex;
    flex-flow: wrap;
    justify-content: center; }
    .ContactData form div {
      flex-basis: 45%;
      width: 200px;
      margin: 0 20px; }
  .ContactData .Logo {
    height: 50px;
    left: 0;
    margin: 50px 0 0 50px;
    position: absolute;
    top: 0;
    width: 100px; }
  .ContactData .CloseBtn {
    right: 0;
    margin: 50px 50px 0 0;
    position: absolute;
    top: 0;
    font-size: 50px;
    color: #d8b093;
    cursor: pointer; }



