.Signing button {
  font-size: 14px;
  border: 1px solid transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #383838;
  padding: 6px 12px;
  color: white;
  margin: 0 5px; }
  .Signing button:focus {
    outline-color: #d8b093; }
  .Signing button:hover {
    cursor: pointer; }

.Signing .SignUp {
  background: #d8b093; }



