@charset "UTF-8";
.Slider {
  height: 100vh;
  position: relative;
  z-index: 50; }
  .Slider .SliderList {
    position: relative;
    height: 100%; }
    .Slider .SliderList .Hide {
      opacity: 0; }

@media screen and (max-width: 920px) {
  .Slider {
    height: 50vh; } }

.SliderDots {
  position: absolute;
  right: 50px;
  top: 50%;
  transform: translateY(-50%); }
  .SliderDots li {
    width: 20px;
    height: 20px;
    margin: 5px 0;
    position: relative; }
    .SliderDots li::before {
      content: "◆";
      color: #d8b093;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 20px; }
  .SliderDots .Active::before {
    content: "◆";
    color: black; }

@media screen and (max-width: 920px) {
  .SliderDots {
    position: static;
    margin: 25px 0;
    text-align: center; }
    .SliderDots li {
      display: inline-block; } }



